*{
  scroll-behavior: smooth;
}
body, html {
  width: 100%;
  height: 100%;
}

.autoWritter {
  font-family: arial, serif;
  display: table;
}
.autoWrite-text {
  display: inline-block;
  overflow: hidden;
  white-space: nowrap;
  letter-spacing: 2px;
  /*-- Call text and cursor animation --*/
  animation: write 2s steps(40, end), cursor .7s step-end infinite ;
  font-size: 32px;
  border-right: 4px solid;
}

/*-- Write text as Animated --*/
@keyframes write {
  from {
    width: 0%
  }
  to {
    width: 100%
  }
}

/*-- Animated blink cursor CSS--*/
@keyframes cursor{
  from, to {
    border-color: transparent;
  }
  50% {
    border-color: darkgrey;
  }

}
